@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  color: #000000;
}

html,
body,
#root,
.App {
  height: 100%;
}

body {
  background: #f2f2f2;
}

a:visited {
  color: #000000;
}

.closed {
  display: none !important;
}

.wide-container {
  width: 1112px;
  margin: 0 auto;
}

.Layout {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.input-error {
  max-width: 100%;
  color: red;
  font-family: "Fira Sans";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 5px;
}/*# sourceMappingURL=index.css.map */